.navbar {
  display: flex;
  justify-content: space-around;
  background-color: #282c34;
  padding: 1rem;
}

.navbar ul {
  display: flex;
  list-style-type: none;
}

.navbar li {
  margin: 0 1rem;
}

.navbar a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
  transition: color 0.3s;
}

.navbar a:hover {
  color: #61dafb;
}
